	module.exports = {"data":{"post":{"html":"<p>Today the <a href=\"http://www.papascott.de/2003/03/25/2134.php\">police action from Monday</a> appears in a better light. The police apparently warned demonstrators for 20 minutes that water cannons would be used before breaking up the protest. The <a title=\"Schüler-Demo: Erste Anzeigen\" href=\"http://www.abendblatt.de/daten/2003/03/26/138306.html\">Abendblatt</a> reports that only two official complaints of police misconduct have been filed. Meanwhile, official investigations have been launched... both into the police action itself, and whether teachers encouraged students to skip school and participate in the protest.</p>\n<p>One still has to wonder... throughout Germany there have been massive, peaceful student protests against the war. Only in Hamburg did the protests turn violent. Can you tell us why, Senator 'Law-and-Order' Schill?</p>","fields":{"title":"Water Cannons Continued","date":"26 Mar 2003","url":"https://papascott.de/2003-03-26-water-cannons-continued/"}}},"pathContext":{"slug":"/2003-03-26-water-cannons-continued/","prev":{"fields":{"title":"If The Americans Think...","date":"26 Mar 2003","slug":"/2003-03-26-if-the-americans-think/"}},"next":{"fields":{"title":"Der Brand: Weapon, Strategy","date":"27 Mar 2003","slug":"/2003-03-27-der-brand-weapon-strategy/"}}}}

